<template>
  <view>
    <view class="logo">
      <view class="img">
        <image
          mode="widthFix"
          src="data:image/png;base64,"
        ></image>
      </view>
    </view>
    <!-- 账号密码输入框 -->
    <view class="form">
      <view class="username">
        <input
          placeholder="请输入手机号"
          v-model="phoneNumber"
          placeholder-style="color: rgba(255,255,255,0.8);"
        />
      </view>
      <view class="password">
        <input
          placeholder="请输入密码"
          v-model="passwd"
          password="true"
          placeholder-style="color: rgba(255,255,255,0.8);"
        />
      </view>
      <view class="btn" @tap="doLogin">登 录</view>
      <view class="res">
        <view @tap="toPage('register')">用户注册</view>
        <view @tap="toPage('resetpasswd')">忘记密码</view>
      </view>
    </view>
    <!-- 第三方登录 -->
    <view class="oauth" v-if="isShowOauth">
      <view class="text">— 快速登录 —</view>
      <view class="list">
        <view
          @tap="oauthLogin('weixin')"
          v-if="showProvider.weixin"
          class="icon weixin"
        ></view>
        <view
          @tap="oauthLogin('qq')"
          v-if="showProvider.qq"
          class="icon qq"
        ></view>
        <view
          @tap="oauthLogin('sinaweibo')"
          v-if="showProvider.sinaweibo"
          class="icon sinaweibo"
        ></view>
        <!-- <view @tap="oauthLogin('xiaomi')" v-if="showProvider.xiaomi" class="icon xiaomi"></view> -->
      </view>
    </view>
  </view>
</template>

<script>
import md5 from "@/common/SDK/md5.min.js";
export default {
  data() {
    return {
      phoneNumber: "",
      passwd: "",
      isShowOauth: true,
      showProvider: {
        weixin: false,
        qq: false,
        sinaweibo: false,
        xiaomi: false,
      },
    };
  },
  onShow() {},
  onLoad(options) {
    const path = options.redirect;
    if (path) {
      this.$store.dispatch("saveRedirectPath", path);
    }
    //APP显示第三方登录
    // #ifdef APP-PLUS
    this.isShowOauth = true;
    // #endif
    this.getProvider();
  },
  methods: {
    oauthLogin(provider) {
      uni.showLoading();
      //第三方登录  先获取用户信息 然后拿着用户信息 给后端发请求注册
      wx.getUserProfile({
        desc: "用于完善会员资料",
        // 获取信息成功的回调
        success: (infoRes) => {
          // 发送login请求
          uni.login({
            provider: provider,
            success: async (res) => {
              // login成功的回调  获取code  然后向后端发请求注册
              const result = await this.$API(
                "/users/wxlogin",
                {
                  code: res.code,
                  userInfo: infoRes.userInfo,
                },
                "POST"
              );
              // 如果返回200  想storage存储用户信息
              if (result.code === 200) {
                uni.setStorage({
                  key: "UserInfo",
                  data: {
                    username: result.data.userInfo.username,
                    face: result.data.userInfo.avatarUrl,
                    signature: "个性签名",
                    integral: 0,
                    balance: 0,
                    envelope: 0,
                  },
                });
                uni.setStorage({
                  key: "Token",
                  data: result.token,
                });
                uni.hideLoading();
                uni.showToast({ title: "登录成功", icon: "success" });
                setTimeout(function () {
                  uni.navigateBack();
                }, 300);
              }
            },
            fail: (e) => {
              console.log("fail: " + JSON.stringify(e));
            },
          });
        },
      });
    },
    getProvider() {
      //获取第三方登录服务
      uni.getProvider({
        service: "oauth",
        success: (res) => {
          let len = res.provider.length;
          for (let i = 0; i < len; i++) {
            //有服务才显示按钮图标
            this.showProvider[res.provider[i]] = true;
          }
          if (res.provider.length == 0) {
            this.isShowOauth = false;
          }
        },
      });
    },
    toPage(page) {
      uni.hideKeyboard();
      uni.navigateTo({
        url: page,
      });
    },
    async doLogin() {
      uni.hideKeyboard();
      //验证手机号码
      if (!/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNumber)) {
        uni.showToast({ title: "请填写正确手机号码", icon: "none" });
        return false;
      }
      uni.showLoading({
        title: "提交中...",
      });
      //模板示例比对本地储存的用户信息，实际使用中请替换为上传服务器比对。
      // 	setTimeout(() => {
      // 		let md5PW = md5(this.passwd);
      // 		uni.getStorage({
      // 			key: 'UserList',
      // 			success: res => {
      // 				for (let i in res.data) {
      // 					let row = res.data[i];
      // 					if (row.username == this.phoneNumber) {
      // 						uni.hideLoading();
      // 						//比对密码
      // 						if (md5PW == res.data[i].passwd) {
      // 							uni.showToast({ title: '登录成功', icon: 'success' });
      // 						} else {
      // 							uni.showToast({ title: '账号或密码不正确', icon: 'none' });
      // 						}
      // 					}
      // 				}
      // 			},
      // 			fail: function(e) {
      // 				uni.hideLoading();
      // 				uni.showToast({ title: '手机号码未注册', icon: 'none' });
      // 			}
      // 		});
      // 	}, 1000);
      const res = await this.$API(
        "/users/login",
        {
          username: this.phoneNumber,
          password: this.passwd,
        },
        "POST"
      );
      uni.hideLoading();
      if (res.code === 200) {
        uni.setStorage({
          key: "UserInfo",
          data: {
            username: res.data.userInfo.username,
            face: "../../../static/img/user/avatar.gif",
            signature: "个性签名",
            integral: 0,
            balance: 0,
            envelope: 0,
          },
        });
        uni.setStorage({
          key: "Token",
          data: res.token,
        });

        uni.showToast({ title: "登录成功", icon: "success" });
        wx.reLaunch({
          url: this.$store.state.users.redirect,
        });
      } else {
        uni.showToast({ title: "账号或密码不正确", icon: "none" });
      }
    },
  },
};
</script>

