  .res {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100upx;
    color: rgba($color: #ffffff, $alpha: 0.8);
  }
}
.oauth {
  @media all and (max-height: 150vw) {
    display: none;
  }
  position: absolute;
  bottom: 50upx;
  width: 100%;
  .text {
    width: 100%;
    height: 60upx;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgba($color: #ffffff, $alpha: 0.8);
    font-size: 28upx;
  }
  .list {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20upx 0;
    .icon {
      font-size: 80upx;
      margin: 0 30upx;
    }
  }
}
</style>
